import React, { useState, useEffect } from "react";
import Icon from "@material-ui/core/Icon";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";

const useStyles = makeStyles({
  wholebutton: {
    marginTop: 25
  },
  button: {
    marginLeft: 10
  }
});

export default function Featured(props) {
  const [single, setSingle] = useState([]);

  useEffect(() => {
    getSingleProduct(props.match.params.slug);
  }, []);

  const classes = useStyles();
  //var backendHost = "https://casson-gallery.herokuapp.com";
  //var backendHost = "http://localhost:5000";
  var backendHost = "http://dev.casson.uk";

  function getSingleProduct(id) {
    fetch(`${backendHost}/Products/${id}`, {
      headers: {
        Accept: "application/json"
      }
    })
      .then(res => res.json())
      .then(json => {
        //var product = json.products.find(item => item._id === id);
        setSingle(json);
        console.log(json);
      });
  }
  return (
    <div
      className="row container-fluid"
      style={{ margin: "90px 0", padding: "0 6vw" }}
    >
      <div className="col-sm-6">
        <h3 className="category-heading">{single.name}</h3>
        <div id="single">
          <div class="card" style={{ padding: "5em 2em 2em 2em" }}>
            <img src={single.image} class="card-img-top" alt="..." />
          </div>
        </div>
      </div>
      <div className="col-sm-6" style={{ margin: "auto" }}>
        <h2>
          {" "}
          This page will contain detailed information about this single Product
        </h2>
        <Link to="/">
          <Button
            variant="contained"
            color="primary"
            className={classes.wholebutton}
          >
            <Icon className="fa fa-home" color="secondary" />
            <span className={classes.button}>Back to Homepage </span>
          </Button>
        </Link>
      </div>
    </div>
  );
}
